import VueContentPlaceholders from 'vue-content-placeholders';
import { UploaderPlugin } from '@syncfusion/ej2-vue-inputs';
import VueApexCharts from 'vue-apexcharts';
import axios from 'axios';
Vue.component('apexchart', VueApexCharts);
Vue.use(UploaderPlugin);
Vue.use(VueContentPlaceholders);
var groupId = window.themeDisplay !== undefined ? window.themeDisplay.getScopeGroupId() : 0;
var companyId = window.themeDisplay !== undefined ? window.themeDisplay.getCompanyId() : 0;
var userId = window.themeDisplay !== undefined ? window.themeDisplay.getUserId() : 0;
var userName = window.themeDisplay !== undefined ? window.themeDisplay.getUserName() : '';
var portalURL = window.themeDisplay !== undefined ? window.themeDisplay.getPortalURL().replace('http://', '') : 'localhost:8080';
var token = window.themeDisplay !== undefined ? window.Liferay.authToken : '';
var portalURLSock = portalURL.indexOf(':') > 0 ? portalURL.substr(0, portalURL.indexOf(':')) : portalURL; // let portalUrlSocket = window.themeDisplay.getPortalURL().replace('https://', '').replace('http://', '')
// Vue.use(VueNativeSock, 'ws://' + portalUrlSocket + '/o/v1/socket/web?groupId='+ groupId
//   + '&portalURL=' + portalUrlSocket
//   + '&companyId=' + companyId
//   + '&userId=' + userId
